import React, { useContext, useState, useEffect, useRef } from 'react';
import { makeStyles, withStyles, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Link, Switch, Route, Redirect, useHistory } from 'react-router-dom';
import Link2 from '@material-ui/core/Link';
import { generatePath } from "react-router";
import DMSwitch from '@material-ui/core/Switch';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Hidden from '@material-ui/core/Hidden';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Chip from '@material-ui/core/Chip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Button from '@material-ui/core/Button';
import ButtonBase from '@material-ui/core/ButtonBase';
import Tooltip from '@material-ui/core/Tooltip';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AutorenewIcon from '@material-ui/icons/Autorenew';
import IconButton from '@material-ui/core/IconButton';
import { useLocalStore } from "../services/LocalStoreService";
import { FiatContext } from '../context/FiatContext';
import { ConfigurationContext } from '../context/ConfigurationContext';
import { ServerContext } from '../context/ServerContext';
import { MessageContext } from '../context/MessageContext';
import { routesUp, routesDown } from '../routes/Routes';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import ReactMarkdown from 'react-markdown';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import pckg2 from '../version2.json';
import { socket } from '../context/SocketContext';
import mixpanel from 'mixpanel-browser';

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  logo: {
    maxWidth: 60,
    padding: 10,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    height: '100%',
  },
  drawerContainer: {
    overflow: 'auto',
  },
  settingsPaper: {
    width: '100%',
    height: '100%',
  },
  content: {
    justify: "flex-start",
    alignItems: "flex-start",
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  footer: {
    // padding: theme.spacing(1),
    marginTop: 'auto',
    backgroundColor: '#000000',
  },
  bottomPush: {
    position: "fixed",
    width: '100%',
    bottom: 0,
    paddingBottom: 0,
    zIndex: theme.zIndex.drawer + 1,
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
  bottomPush2: {
    position: "fixed",
    bottom: 0,
    paddingBottom: 70,
  },
  smallScreenToggles: {
    maxWidth: '100%',
    bottom: 0,
    paddingBottom: 0,
    paddingTop: 24,
  },
}));

function Copyright() {
  return (
    <Box color="#FFF" height={54} p={2}>
      <Typography variant="body2" align="center">
        {'Copyright © '}
        <Link2 style={{ color: '#ffffff', textDecoration: 'none' }} href="https://hodlerhacks.com/" target="_blank" rel="noopener">
          hodlerhacks.com
        </Link2>{' '}
        {new Date().getFullYear()}
      </Typography>
    </Box>
  );
}

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    boxShadow: theme.shadows[1],
    fontSize: 12,
  },
}))(Tooltip);

export default function Main() {
  const history = useHistory();
  const version = pckg2.version;
  const bbv = pckg2.version;
  const checkNow = useRef(false);

  const [mode, setMode] = useLocalStore('mode', 'light');
  const setFiatStore = useLocalStore('fiat', 'EUR')[1];
  const [fiat, setFiat] = useContext(FiatContext);
  const [messages, setMessages] = useContext(MessageContext);
  const configuration = useContext(ConfigurationContext)[0];
  const [modus, setModus] = useState('manual');
  const [{ status, serverversion, servertime, access, publicKey, userid, validInstallation }, setServer] = useContext(ServerContext);
  const [openUpdate, setOpenUpdate] = useState(false);
  const [outdated, setOutdated] = useState(false);
  const [open, setOpen] = useState(true);
  const setDisconnected = useState(false)[1];
  const [modusSwitchDisabled, setModusSwitchDisabled] = useState(window.location.pathname.includes('/configuration'));
  const [flexWidthDown, setFlexWidthDown] = useState(0);
  const [flexWidthUp, setFlexWidthUp] = useState(0);
  const [overlayMenu, setOverlayMenu] = useState(false);
  const [latestVersion, setLatestVersion] = useLocalStore('latest-version', version) || '0.0.0';
  const [lastMsg, setLastMsg] = useLocalStore('last-msg', 0);
  const [dialog, setDialog] = useState({ number: 0, title: '', message: '', lock: false });
  const [openDialog, setOpenDialog] = useState(false);
  const [maxWidth, setMaxWidth] = useState('sm');
  const [checkIconColor, setcheckIconColor] = useState('#fff');
  const lastRef = useRef(lastMsg);

  const getBotPath = () => {
    const pathname = window.location.pathname;
    const result = pathname.match(/bot[0-9]{3}/);

    if (result)
      return result[0];
    else
      return null;
  }

  const [botPath, setBotPath] = useState(getBotPath());

  const getPath = (page) => {
    if (botPath) {
      return generatePath("/:bot/:page", {
        bot: botPath,
        page: page,
      });
    } else {
      return '';
    }
  }

  const handleRefresh = () => {
    window.location.reload();
  }

  useEffect(() => {
    setDisconnected(status === 'disconnected' && Object.keys(configuration).length !== 0);

    return () => {
    }
  }, [status]);

  useEffect(() => {
    const url = window.location.origin;
    const localhost = url.includes('localhost');

    // Mixpanel
    if (userid) {
      mixpanel.init('6b67f3d61ee94b8f5c80b4affef4c4d9',
        {
          host: "api-eu.mixpanel.com",
        });
      mixpanel.identify(userid);
      mixpanel.track('Dashboard', {
        'localhost': localhost,
      });
    }

    return () => {
    }
  }, [userid]);

  useEffect(() => {
    document.title = `BB | Bot Dashboard`;

    // Listen to local store changes (when toggled on another page)
    window.addEventListener('storage', () => {
      const newMode = localStorage.getItem('mode');
      setMode(newMode);
      const newFiat = localStorage.getItem('fiat');
      setFiat(newFiat);

      const newVersion = localStorage.getItem('latest-version');
      setLatestVersion(newVersion);

      const newLast = localStorage.getItem('last-msg');

      if (newLast !== lastRef.current) {
        lastRef.current = newLast;

        let newMessages = [];

        // Only keep messages that haven't been shown yet (on any page)
        messages.forEach(msg => {
          if (msg.number > newLast) newMessages.push(msg);
        });

        setMessages(newMessages);
      }
    });

    return () => {
    }
  }, []);

  useEffect(() => {
    setOpen(status !== 'connected' || Object.keys(configuration).length === 0);
    return () => {
    }
  }, [status, configuration]);

  useEffect(() => {
    setModus(configuration.headless ? 'auto' : 'manual');

    return () => {
    }
  }, [configuration]);

  useEffect(() => {
    const unlisten = history.listen((location) => {
      setModusSwitchDisabled(location.pathname.includes('/configuration'));
      setBotPath(getBotPath());
    });
    // Clean up the event listener
    return () => {
      unlisten();
    };
  }, [history])

  useEffect(() => {
    const outdated = (serverversion && (serverversion !== '0.0' && serverversion !== version));
    setOutdated(outdated);

    return () => {
    }
  }, [serverversion]);

  const isNewerVersion = (newVer, oldVer) => {
    const oldParts = oldVer.split('.')
    const newParts = newVer.split('.')
    for (var i = 0; i < newParts.length; i++) {
      const a = ~~newParts[i] // parse int
      const b = ~~oldParts[i] // parse int
      if (a > b) return true
      if (a < b) return false
    }
    return false
  }

  useEffect(() => {
    let wdtDown = 0;
    let wdtUp = 0;
    const update = isNewerVersion(latestVersion, version);
    const logout = access && access.token;

    if (update) {
      wdtDown += 140;
    }
    if (logout) {
      wdtDown += 50;
      wdtUp += 50;
    }

    setFlexWidthDown(wdtDown);
    setFlexWidthUp(wdtUp);

    return () => {
    }
  }, [latestVersion, version, access]);

  const classes = useStyles();

  const handleDarkmode = (e) => {
    (mode === 'light') ? setMode('dark') : setMode('light');
  }

  const handleFiat = (e) => {
    if (fiat === 'EUR') {
      setFiat('USD');
      setFiatStore('USD');
    } else {
      setFiat('EUR');
      setFiatStore('EUR');
    }
  }

  const handleModus = (e) => {
    const newModus = (modus === 'auto') ? 'manual' : 'auto';
    const headless = (newModus === 'auto');
    setModus(newModus);
    if (socket && access.allow) socket.emit("store-headless", {
      headless: headless,
    }, access.token);
  }

  const theme = createMuiTheme({
    palette: {
      type: mode
    }
  });

  const StyledButton = withStyles({
    root: {
      '&:hover': {
        backgroundColor: 'transparent',
      },
      '&:click': {
        backgroundColor: '#f00',
      },
      justifyContent: 'flex-start',
    },
  })(Button);

  const onLogout = () => {
    if (socket) socket.emit("logout", access.token);
    setServer({
      status: status,
      serverversion: serverversion,
      servertime: servertime,
      access: {
        allow: false,
        reason: 'logged out',
        token: null,
      },
      publicKey: publicKey,
    });
  };

  const backHome = () => {
    let openUrl = window.location.origin;
    if (openUrl.includes(':4000')) openUrl = openUrl.replace(':4000', ':5000'); // For development only

    window.open(openUrl, '_blank', 'noopener');
  }

  const cleanBotname = (name) => {
    if (name && name.charAt(0) === '^') name = name.slice(1);

    return name;
  }

  const toggleOverlayMenu = (open) => {
    setOverlayMenu(open);
  }

  const checkForUpdate = () => {
    checkNow.current = true;
    getMessages();
  }

  const handleUpdateOpen = () => {
    setOpenUpdate(true);
  }

  const confirmBbUpdate = () => {
    setOpenUpdate(false);

    if (socket) socket.emit("update-bb", access.token);
  }

  const handleUpdateClose = () => {
    setOpenUpdate(false);
  }

  const handleCloseDialog = async (e) => {
    const last = e.target.parentElement.id;
    setOpenDialog(false);

    // Remove shown message from array
    const newMessages = messages.slice(1, messages.length);
    setMessages(newMessages);

    if (socket) socket.emit("confirm-message", last, access.token);

    setLastMsg(last);
  };

  const sleep = (ms) => {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  useEffect(() => {
    if (messages.length > 0) {
      const msg = messages[0];
      if (msg.bbvNew) setLatestVersion(msg.bbvNew);

      /// Only show message in case it matches current version, major version, or any
      if (msg.bbvReceive === 'any' || bbv.startsWith(msg.bbvReceive)) {

        // Check if the message contains a new version number, and if so, if BB is already on the new version
        let alreadyUpdated = msg.bbvNew ? bbv >= msg.bbvNew : false;

        // If already on the latest version, don't show the message
        if (!alreadyUpdated) {
          if (msg.dialogWidth === null) {
            const textLength = msg.message.length;
            if (textLength < 400) setMaxWidth('sm');
            else if (textLength < 800) setMaxWidth('md');
            else if (textLength < 3000) setMaxWidth('lg');
            else setMaxWidth('xl');
          } else {
            setMaxWidth(msg.dialogWidth);
          }

          const newDialogMsg = {
            number: msg.number,
            title: msg.title,
            message: msg.message,
            width: msg.dialogWidth,
            lock: msg.lock,
            updated: alreadyUpdated,
          }

          setDialog(newDialogMsg);
          setOpenDialog(true);
        } else {
          if (socket) socket.emit("confirm-message", msg.number, access.token);
          // Remove shown message from array
          const newMessages = messages.slice(1, messages.length);
          setMessages(newMessages);
        }
      } else {
        if (socket) socket.emit("confirm-message", msg.number, access.token);
        // Remove message from array
        const newMessages = messages.slice(1, messages.length);
        setMessages(newMessages);
      }
    } else {
      setOpenDialog(false);
    }
  }, [messages]);

  async function getMessages() {
    if (socket) socket.emit("get-messages", access.token);

    if (checkNow.current) {
      setcheckIconColor('#ff9800');
      setTimeout(() => {
        setcheckIconColor('#fff');
      }, 1500);
    }

    checkNow.current = false;
  }

  const SideMenu = (props) => {
    const showToolbar = props.showToolbar;
    return (
      <Grid item xs={12}>
        {showToolbar && (
          <Toolbar />
        )}
        {(configuration.configstate !== 'initial' && configuration.configstate !== 'recovery' && configuration.configstate !== 'loading-canceled') ? (
          <div className={classes.drawerContainer}>
            <List>
              {routesUp.map((prop, key) => {
                return (
                  <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} onClick={() => toggleOverlayMenu(false)} key={key}>
                    <ListItem button key={prop.sidebarName}>
                      <ListItemIcon><prop.icon /></ListItemIcon>
                      <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                    </ListItem>
                  </Link>
                );
              })}
            </List>
            <Divider />
            {routesDown.map((prop, key) => {
              return (
                <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} onClick={() => toggleOverlayMenu(false)} key={key}>
                  <ListItem button key={prop.sidebarName}>
                    <ListItemIcon><prop.icon /></ListItemIcon>
                    <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                  </ListItem>
                </Link>
              );
            })}
          </div>
        ) : (
          <div className={classes.drawerContainer}>
            <List>
              {routesUp.map((prop, key) => {
                return (
                  <Redirect to={getPath('configuration')} style={{ textDecoration: 'none' }} onClick={() => toggleOverlayMenu(false)} key={key}>
                    <ListItem button key={prop.sidebarName}>
                      <ListItemIcon><prop.icon /></ListItemIcon>
                      <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                    </ListItem>
                  </Redirect>
                );
              })}
            </List>
            <Divider />
            {routesDown.slice(0, 1).map((prop, key) => {
              return (
                <Link to={getPath(prop.path)} style={{ textDecoration: 'none' }} onClick={() => toggleOverlayMenu(false)} key={key}>
                  <ListItem button key={prop.sidebarName}>
                    <ListItemIcon><prop.icon /></ListItemIcon>
                    <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                  </ListItem>
                </Link>
              );
            })}
            {routesDown.slice(1, routesDown.length).map((prop, key) => {
              return (
                <Redirect to={getPath('configuration')} style={{ textDecoration: 'none' }} onClick={() => toggleOverlayMenu(false)} key={key}>
                  <ListItem button key={prop.sidebarName}>
                    <ListItemIcon><prop.icon /></ListItemIcon>
                    <ListItemText primary={prop.sidebarName} primaryTypographyProps={{ variant: 'button', color: 'textPrimary' }} />
                  </ListItem>
                </Redirect>
              );
            })}
          </div>
        )}
      </Grid>
    )
  }

  return (<ThemeProvider theme={theme}>
    {!outdated ? (
      <React.Fragment>
        <Backdrop className={classes.backdrop} open={open}>
          <CircularProgress color="inherit" />
        </Backdrop>
        <Dialog
          open={openDialog}
          fullWidth={true}
          maxWidth={maxWidth}
        >
          <DialogTitle id="dialog-title">{dialog.title}</DialogTitle>
          <DialogContent>
            <DialogContentText id="dialog-description">
              <ReactMarkdown linkTarget="_blank">
                {dialog.message}
              </ReactMarkdown>
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button variant="contained" id={dialog.number} onClick={handleCloseDialog} color="secondary" disabled={dialog.lock}>
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <div className={classes.root}>
          <CssBaseline />
          <AppBar position="fixed" className={classes.appBar}>
            <Toolbar>
              <Hidden lgUp>
                <Grid item>
                  <LightTooltip title="Menu">
                    <IconButton onClick={() => toggleOverlayMenu(true)}>
                      <MenuIcon style={{ color: '#fff' }} />
                    </IconButton>
                  </LightTooltip>
                </Grid>
              </Hidden>
              <ButtonBase onClick={backHome}>
                <img src="/logo192.png" alt="Balance Bot Logo" className={classes.logo} />
              </ButtonBase>
              <Grid container direction="row" alignItems="center" nowrap="true">
                <Grid item xs zeroMinWidth>
                  <Box mt={1.5}>
                    <Typography variant="h6" color="inherit" noWrap>
                      Balance Bot | {cleanBotname(configuration.botname)}
                    </Typography>
                  </Box>
                  <Box mt={-1} style={{ color: '#959FD6' }}>
                    <Typography variant="caption" color="inherit" noWrap>
                      v{version}
                    </Typography>
                  </Box>
                </Grid>
                <Hidden xsDown>
                  <FlexToolbar wdt={flexWidthDown} update={isNewerVersion(latestVersion, version)} logout={access && access.token} onLogout={onLogout} handleUpdateOpen={handleUpdateOpen} checkForUpdate={checkForUpdate} checkIconColor={checkIconColor} />
                </Hidden>
                <Hidden smUp>
                  <FlexToolbar wdt={flexWidthUp} update={isNewerVersion(latestVersion, version)} logout={access && access.token} onLogout={onLogout} handleUpdateOpen={handleUpdateOpen} checkForUpdate={checkForUpdate} checkIconColor={checkIconColor} />
                </Hidden>
              </Grid>
            </Toolbar>
          </AppBar>
          <Hidden mdDown>
            <Drawer className={classes.drawer} variant="permanent">
              <Paper className={classes.drawerPaper}>
                <Grid container direction="column">
                  <SideMenu showToolbar={true} />
                  <div className={classes.bottomPush2}>
                    <Box pl={1}>
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                        <Grid item xs={8}>
                          <FormControlLabel
                            onChange={handleDarkmode}
                            value={mode}
                            control={<DMSwitch checked={mode === 'dark'} color="secondary" />}
                            label={<Typography variant="button">Light&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={4}>
                          <StyledButton disableRipple onClick={handleDarkmode}><Typography variant="button">Dark</Typography></StyledButton>
                        </Grid>
                      </Grid>
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                        <Grid item xs={8}>
                          <FormControlLabel
                            onChange={handleFiat}
                            value={fiat}
                            control={<DMSwitch checked={fiat === 'EUR'} color="secondary" />}
                            label={<Typography variant="button">USD&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={4}>
                          <StyledButton disableRipple onClick={handleFiat}><Typography variant="button">EUR</Typography></StyledButton>
                        </Grid>
                      </Grid>
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ width: '200px' }}>
                        <Grid item xs={8}>
                          <FormControlLabel
                            disabled={modusSwitchDisabled}
                            onChange={handleModus}
                            value={modus}
                            control={<DMSwitch checked={modus === 'auto'} color="secondary" />}
                            label={<Typography variant="button">Manual</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={4}>
                          <StyledButton disableRipple disabled={modusSwitchDisabled} onClick={handleModus}><Typography variant="button">Auto</Typography></StyledButton>
                        </Grid>
                      </Grid>
                    </Box>
                  </div>
                </Grid>
              </Paper>
            </Drawer>
          </Hidden>
          <Hidden lgUp>
            {overlayMenu && (
              <Drawer className={classes.drawer} open={overlayMenu} onClose={() => toggleOverlayMenu(false)} variant="temporary">
                <Paper className={classes.drawerPaper}>
                  <Grid item>
                    <LightTooltip title="Close">
                      <IconButton onClick={() => toggleOverlayMenu(false)}>
                        <CloseIcon />
                      </IconButton>
                    </LightTooltip>
                  </Grid>
                  <Grid container direction="column">
                    <SideMenu showToolbar={false} />
                  </Grid>
                </Paper>
              </Drawer>
            )}
          </Hidden>
          <main className={classes.content}>
            <Toolbar />
            <Switch>
              <Route exact path={'/:bot'}>
                {(configuration.configstate !== 'initial' && configuration.configstate !== 'recovery' && configuration.configstate !== 'loading-canceled') ? (
                  <Redirect to={getPath('portfolio')} />
                ) : (
                  <Redirect to={getPath('configuration')} />
                )}
              </Route>
              {routesUp.map((prop, key) => {
                return (
                  <Route exact path={'/:bot/' + prop.path} component={prop.component} key={key} />
                );
              })}
              {routesDown.map((prop, key) => {
                return (
                  <Route exact path={'/:bot/' + prop.path} component={prop.component} key={key} />
                );
              })}
            </Switch>
            <Hidden lgUp>
              <div className={classes.smallScreenToggles}>
                <Paper className={classes.settingsPaper}>
                  <Box pl={1}>
                    <Grid container direction="column">
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                        <Grid item xs={7}>
                          <FormControlLabel
                            onChange={handleDarkmode}
                            value={mode}
                            control={<DMSwitch checked={mode === 'dark'} color="secondary" />}
                            label={<Typography variant="button">Light&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={3}>
                          <StyledButton disableRipple onClick={handleDarkmode}><Typography variant="button">Dark</Typography></StyledButton>
                        </Grid>
                        <Grid item xs={2}></Grid>
                      </Grid>
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                        <Grid item xs={7}>
                          <FormControlLabel
                            onChange={handleFiat}
                            value={fiat}
                            control={<DMSwitch checked={fiat === 'EUR'} color="secondary" />}
                            label={<Typography variant="button">USD&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={3}>
                          <StyledButton disableRipple onClick={handleFiat}><Typography variant="button">EUR</Typography></StyledButton>
                        </Grid>
                        <Grid item xs={2}></Grid>
                      </Grid>
                      <Grid container item xs={12} alignItems="center" justify="flex-end" style={{ maxWidth: '220px' }}>
                        <Grid item xs={7}>
                          <FormControlLabel
                            disabled={modusSwitchDisabled}
                            onChange={handleModus}
                            value={modus}
                            control={<DMSwitch checked={modus === 'auto'} color="secondary" />}
                            label={<Typography variant="button">Manual</Typography>}
                            labelPlacement="start"
                          />
                        </Grid>
                        <Grid item xs={3}>
                          <StyledButton disableRipple disabled={modusSwitchDisabled} onClick={handleModus}><Typography variant="button">Auto</Typography></StyledButton>
                        </Grid>
                        <Grid item xs={2}></Grid>
                      </Grid>
                    </Grid>
                  </Box>
                </Paper>
              </div>
            </Hidden>
          </main>
        </div>
        {/* </BrowserRouter> */}
        <div style={{ height: 70 }}>
          &nbsp;
        </div>
        <Dialog open={openUpdate} onClose={handleUpdateClose}>
          {validInstallation ? (
            <>
              <DialogTitle>Confirm Update</DialogTitle>
              <DialogContent>
                <DialogContentText>
                  <Typography color="textPrimary">
                    Do you want to update to the latest version? Balance Bot will be unavailable until the update has completed. This may take a few minutes. Just wait for the Bot Manager to reappear.
                  </Typography>
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" onClick={handleUpdateClose} color={(mode === 'light') ? "primary" : "secondary"}>
                  Cancel
                </Button>
                <Button variant="contained" onClick={confirmBbUpdate} color={(mode === 'light') ? "primary" : "secondary"}>
                  Update
                </Button>
              </DialogActions>
            </>
          ) : (
            <>
              <DialogTitle>Manual Update Required</DialogTitle>
              <DialogContent>
                <DialogContentText>
                  <Typography color="textPrimary">
                    Your installation does not support updating from the browser. Please perform a manual update.
                  </Typography>
                </DialogContentText>
                <DialogContentText>
                  <Typography color="textPrimary">
                    Possible causes:
                    <ol>
                      <li>You haven't installed BB using the official installer</li>
                      <li>You're running BB in a Docker container</li>
                    </ol>
                  </Typography>
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" onClick={handleUpdateClose} color={(mode === 'light') ? "primary" : "secondary"}>
                  OK
                </Button>
              </DialogActions>
            </>
          )}
        </Dialog>
      </React.Fragment >
    ) : (
      <Dialog
        open={true}
        fullWidth={true}
        maxWidth={'sm'}
      >
        <DialogContent>
          <DialogContentText>
            <Typography variant="overline" color="secondary">
              New version has been installed
            </Typography>
            <DialogContentText>
            </DialogContentText>
            <Typography variant="overline" color="textPrimary">
              Please refresh the browser window to load the latest version
            </Typography>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleRefresh} color="secondary">
            Refresh
          </Button>
        </DialogActions>
      </Dialog>
    )
    }
    <div className={classes.bottomPush}>
      <footer className={classes.footer}>
        <Grid container alignItems="center">
          <Grid item xs={false} sm={12} md={12}>
            <Copyright />
          </Grid>
        </Grid>
      </footer>
    </div>
  </ThemeProvider >
  )
}

const FlexToolbar = (props) => {
  const { wdt, update, logout, onLogout, handleUpdateOpen, checkForUpdate, checkIconColor } = props;

  return (
    <Grid item xs container direction="row" alignItems="center" justify="flex-end" style={{ maxWidth: wdt }}>
      {(update) ? (
        <Hidden xsDown>
          <Grid item style={{ width: '130px' }}>
            <Chip onClick={handleUpdateOpen} label='Update Available' style={{ backgroundColor: '#959FD6', color: 'white' }} />
          </Grid>
        </Hidden>
      ) : (
        <Hidden xsDown>
          <Grid item style={{ width: '40px' }}>
            <LightTooltip title="Check for updates">
              <IconButton onClick={checkForUpdate}>
                <AutorenewIcon style={{ color: checkIconColor }} />
              </IconButton>
            </LightTooltip>
          </Grid>
        </Hidden>
      )}
      {(logout) && (
        <Grid item style={{ width: '48px' }}>
          <LightTooltip title="Logout">
            <IconButton onClick={onLogout}>
              <ExitToAppIcon style={{ color: '#ffffff' }} />
            </IconButton>
          </LightTooltip>
        </Grid>
      )}
    </Grid>
  );
}